.compare-label_second { background-color: #1853ce; }
.compare-label_third { background-color: #2579e8; }
.compare-label_fourth { background-color: #3e97f9; }
.compare-label_fifth { background-color: #66daff; }
.compare-label__step { position: absolute; top: 50%; left: 10px; width: 40px; height: 40px; transform: translateY(-50%); border: 1px solid #fff; border-radius: 50%; font-size: 30px; font-weight: 700; text-align: center; line-height: 38px; }

.diagnostic { padding-top: 39px; padding-bottom: 73px; }
@media (max-width: 767px) { .diagnostic { padding-top: 40px; padding-bottom: 30px; } }
.diagnostic .title { margin-bottom: 47px; }
.diagnostic__title { margin-bottom: 18px; }
.diagnostic__descr { position: relative; }
.diagnostic__img { position: absolute; bottom: -102px; left: -225px; pointer-events: none; }
@media (max-width: 991px) { .diagnostic__img { display: none; } }
.diagnostic__text { margin-bottom: 15px; }
.diagnostic .custom-list__item { margin-bottom: 20px; }
.diagnostic__symptomps { position: relative; max-width: 370px; margin: 95px 0 30px 130px; padding: 29px 15px 30px 30px; background-color: #fff; box-shadow: 9.744px 2.25px 30px 0 rgba(15, 13, 11, 0.07); z-index: 1; }
@media (max-width: 1200px) { .diagnostic__symptomps { margin-left: 40px; } }
@media (max-width: 991px) { .diagnostic__symptomps { margin: 30px auto; } }
.diagnostic__symptomps .diagnostic__title { font-size: 24px; }
.diagnostic__symptomps .custom-list__item { margin-bottom: 12px; padding-left: 45px; font-size: 14px; }
.diagnostic__symptomps .custom-list__item:before { left: 10px; }

.methods { padding-top: 60px; padding-bottom: 70px; background: url("../img/bg-6.png") no-repeat center top/cover; }
@media (max-width: 767px) { .methods { padding-top: 40px; padding-bottom: 30px; } }
.methods .title { margin-bottom: 48px; color: #fff; }
.methods .slider-item { margin: 0 10px; }
.methods .slider-item__img { border-radius: 20px 0 20px 0; }
.methods .slick-arrow { border-color: #fff; color: #fff; }

.prevention { position: relative; padding-top: 80px; padding-bottom: 20px; overflow: hidden; }
@media (max-width: 767px) { .prevention { padding-top: 40px; padding-bottom: 30px; } }
.prevention .title { margin-bottom: 52px; }
.prevention__title { margin-bottom: 37px; }
.prevention__descr { position: relative; }
.prevention__img { position: absolute; top: 7px; left: -280px; max-width: none; pointer-events: none; }
@media (max-width: 767px) { .prevention__img { display: none; } }
.prevention-self { position: relative; max-width: 370px; margin: 13px 0 265px 100px; padding: 29px 15px 30px 30px; background-color: #fff; box-shadow: 9.744px 2.25px 30px 0 rgba(15, 13, 11, 0.07); z-index: 1; }
@media (max-width: 767px) { .prevention-self { margin: 0 auto 20px; } }
.prevention-self .prevention__title { margin-bottom: 15px; font-size: 24px; }
.prevention-self .custom-list__item { margin-bottom: 12px; padding-left: 45px; font-size: 14px; }
.prevention-self .custom-list__item:before { left: 10px; }
.prevention .video { position: absolute; left: 50%; bottom: 0; width: 945px; margin-top: 0; }
@media (max-width: 991px) { .prevention .video { display: none; } }


